.header-edit{font-size:24px;color:#fff}.fixed-bottom{position:fixed;left:0;right:0;bottom:0;padding:15px;background:rgba(255,255,255,0.5)}.discuss_list{padding-left:15px}.discuss_item{display:block;position:relative;padding:10px 15px 10px 60px}.discuss_item:after{content:"\200B";display:block;height:0;clear:both}.discuss_item .user_info{min-height:20px;overflow:hidden}.discuss_item .nickname{display:block;font-weight:400;font-style:normal;color:#727272;width:9em;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;word-wrap:normal}.discuss_item .avatar{position:absolute;top:0;left:0;top:15px;width:50px;height:50px;background-color:#ccc;vertical-align:top;margin-top:0;border-radius:50%}.discuss_item .discuss_message{padding:10px 0;color:#3e3e3e;line-height:1.4}.discuss_item .discuss_extra_info{color:#8c8c8c;font-size:12px}.discuss_item .discuss_extra_info a{margin-left:.5em}.discuss_item .discuss_status{color:#ff7a21;white-space:nowrap}.discuss_item .discuss_status i{font-style:normal;margin-right:2px}.discuss_item .discuss_opr{float:right}.icon-list{overflow:hidden}.icon-list li{float:left;line-height:20px;padding-right:15px;font-size:12px;color:#727272}.icon-list .icon{font-size:16px}.icon-list .icon-yuedu{position:relative;top:2px}


